import HouseError from './HouseError';
import HouseLoading from './HouseLoading';
import HouseLoaded from './HouseLoaded';
import useSWR from 'swr';

const fetcher = url => fetch(url).then(r => r.json());

const House = props => {

  const { houseId } = props.match.params;

  const { data, error } = useSWR(`${process.env.REACT_APP_API_URL}/items/house/${houseId}?fields=*,*.*`, fetcher);

  if (error) {
    return <HouseError />
  } else if (data) {
    return <HouseLoaded {...data.data } />
  } else {
    return <HouseLoading />
  }

}

export default House;