import House from './house/House';
import Search from './search/Search';
import MapView from './map/MapView';

import { 
  BrowserRouter as Router, 
  Link,
  Switch, 
  Route
} from 'react-router-dom';

import './App.scss';

function App() {

  return (
    <Router>      
      <Switch>
        <Route path="/search" component={Search} />
        
        <Route path="/house/:houseId" component={House} />

        <Route path="/map">
          <MapView />
        </Route>


        <Route path="/">
          <p>
            Future site of the Tetrastylon DB interface. Nothing to see yet.
          </p>

          <Link to="/house/65dcf6b5-d0a4-45c9-a466-c072d1351c93">Example house</Link>
        </Route>
      </Switch>
    </Router>
  );

}

export default App;
