import { Tooltip } from 'antd';
import { HomeOutlined, SearchOutlined } from '@ant-design/icons';
import { useHistory } from 'react-router-dom';

const Navbar = props => {

  const history = useHistory();

  return (
    <nav style={{ backgroundImage: 'url("/logo_navbar.png")' }}>
      <div className="nav-item-wrapper" onClick={() => history.push('/search')}>
        <div className="nav-item">
          <Tooltip title="Search">
            <SearchOutlined />
            <label>Search</label>
          </Tooltip>
        </div>
      </div>

      <div className="nav-item-wrapper" onClick={() => history.push('/')}>
        <div className="nav-item">
          <Tooltip title="Home">
            <HomeOutlined />
            <label>Home</label>
          </Tooltip>
        </div>
      </div>
    </nav>
  )

}

export default Navbar;