import { Image, Spin, Table } from 'antd';
import { LoadingOutlined } from '@ant-design/icons';
import { useHistory } from 'react-router-dom';

import './Search.scss';

const getImageSrc = result => 
  result.images?.length > 0 ?
    `${process.env.REACT_APP_API_URL}/assets/${result.images[0].directus_files_id.id}?width=120&height=120` : null; // TODO fallback

const SearchResultsLoaded = props => {

  const { data } = props;

  const history = useHistory();

  const rows = data && data.map(r => ({
    key: r.id,
    image: getImageSrc(r),
    house_name: r.house_name,
    city: r.city,
    constructed: { text: r.construction_date, numeric: r.construction_date_numeric },
    abandonded: { text: r.abandon_date, numeric: r.abandon_date_numeric },
    dimensions: r.house_dimensions,
    complete_plan: r.complete_plan
  }));

  const onClick = row =>
    history.push(`/house/${row.key}`);

  return (
    <Table 
      loading={ props.loading && { indicator: <Spin indicator={ <LoadingOutlined /> } /> }}
      dataSource={rows} 
      pagination={false}
      onRow={row => ({ onClick: evt => onClick(row)}) } >

      <Table.Column title="" dataIndex="image" render={src => 
        <Image src={src} preview={false} /> 
      }/>
      <Table.Column title="House Name" dataIndex="house_name" />
      <Table.Column title="City" dataIndex="city" />
      <Table.Column title="Constructed" dataIndex={['constructed', 'text']} />
      <Table.Column title="Abandonded" dataIndex={['abandonded', 'text']} />
    </Table>
  )

}

export default SearchResultsLoaded;